	module.exports = {"data":{"markdownRemark":{"html":"<!-- TOC -->\n<ul>\n<li><a href=\"#1-th%E1%BB%B1c-hi%E1%BB%87n-thay-%C4%91%E1%BB%95i-c%E1%BB%A7a-user-ngay-l%E1%BA%ADp-t%E1%BB%A9c\">1: Thực hiện thay đổi của user ngay lập tức</a></li>\n<li><a href=\"#2-m%C3%A0n-h%C3%ACnh-settings\">2: Màn hình settings</a></li>\n<li><a href=\"#3-nhi%E1%BB%81u-l%E1%BB%B1a-ch%E1%BB%8Dn\">3: Nhiều lựa chọn</a></li>\n<li><a href=\"#4-khi-thay-%C4%91%E1%BB%95i-l%E1%BB%B1a-ch%E1%BB%8Dn-%E1%BA%A3nh-h%C6%B0%E1%BB%9Fng-%C4%91%E1%BA%BFn-c%C3%A1c-l%E1%BB%B1a-ch%E1%BB%8Dn-kh%C3%A1c\">4: Khi thay đổi lựa chọn ảnh hưởng đến các lựa chọn khác</a></li>\n<li><a href=\"#5-k%E1%BA%BFt-qu%E1%BA%A3-onoff-c%C3%B3-th%E1%BB%83-d%E1%BB%85-d%C3%A0ng-nh%E1%BA%ADn-th%E1%BA%A5y\">5: Kết quả on/off có thể dễ dàng nhận thấy</a></li>\n<li><a href=\"#6-c%C3%A1c-ph%E1%BA%A7n-t%E1%BB%AD-li%C3%AAn-quan-v%E1%BB%9Bi-nhau\">6: Các phần tử liên quan với nhau</a></li>\n<li><a href=\"#7-m%E1%BB%99t-l%E1%BB%B1a-ch%E1%BB%8Dn\">7: Một lựa chọn</a></li>\n</ul>\n<!-- /TOC -->\n<h1 id=\"1-thực-hiện-thay-đổi-của-user-ngay-lập-tức\"><a href=\"#1-th%E1%BB%B1c-hi%E1%BB%87n-thay-%C4%91%E1%BB%95i-c%E1%BB%A7a-user-ngay-l%E1%BA%ADp-t%E1%BB%A9c\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>1: Thực hiện thay đổi của user ngay lập tức</h1>\n<p>Sử dụng Toggle switch khi</p>\n<ul>\n<li>\n<p>Khi thay đổi được thực hiện ngay lập tức không cần thêm một thao tác nào khác nữa</p>\n</li>\n<li>\n<p>Không cần user confirm trước khi thực hiện</p>\n</li>\n</ul>\n<p><img src=\"https://cdn-images-1.medium.com/max/1600/1*eBbKx5ezvvNQKH8bE-0hpA.png\" alt=\"Checkbox hay Toggle switch - gợi ý cho thiết kế\"></p>\n<h1 id=\"2-màn-hình-settings\"><a href=\"#2-m%C3%A0n-h%C3%ACnh-settings\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>2: Màn hình settings</h1>\n<p>Sử dụng checkbox khi</p>\n<ul>\n<li>\n<p>Cần confirm trước khi thay đổi, hoặc user có thể xem lại trước khi thật sự lưu(submit)</p>\n</li>\n<li>\n<p>Trong form có các nút Submit, Ok, Next, Apply để lưu thay đổi.</p>\n</li>\n</ul>\n<p><img src=\"https://cdn-images-1.medium.com/max/1600/1*veJQ87bB5Lt-K-oNcazJcQ.png\" alt=\"Checkbox hay Toggle switch - gợi ý cho thiết kế\"></p>\n<h1 id=\"3-nhiều-lựa-chọn\"><a href=\"#3-nhi%E1%BB%81u-l%E1%BB%B1a-ch%E1%BB%8Dn\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>3: Nhiều lựa chọn</h1>\n<p>Sử dụng checkbox khi</p>\n<ul>\n<li>Có nhiều lựa chọn để user chọn, các lựa chọn này thuộc cùng một category hoặc group.</li>\n</ul>\n<p><img src=\"https://cdn-images-1.medium.com/max/1600/1*IBd2AFjn0aaBbFU0oz6KCw.png\" alt=\"Checkbox hay Toggle switch - gợi ý cho thiết kế\"></p>\n<h1 id=\"4-khi-thay-đổi-lựa-chọn-ảnh-hưởng-đến-các-lựa-chọn-khác\"><a href=\"#4-khi-thay-%C4%91%E1%BB%95i-l%E1%BB%B1a-ch%E1%BB%8Dn-%E1%BA%A3nh-h%C6%B0%E1%BB%9Fng-%C4%91%E1%BA%BFn-c%C3%A1c-l%E1%BB%B1a-ch%E1%BB%8Dn-kh%C3%A1c\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>4: Khi thay đổi lựa chọn ảnh hưởng đến các lựa chọn khác</h1>\n<p>Sử dụng Checkbox khi</p>\n<ul>\n<li>Nếu thay đổi giá trị của checkbox hiện tại, sẽ ảnh hưởng đến các lựa chọn con bên trong. Ví dụ các lựa chọn bên dưới All chỉ xuất hiện (hoặc enable select) khi <em>All</em> được chọn</li>\n</ul>\n<p><img src=\"https://cdn-images-1.medium.com/max/1600/1*RoIVsG5DH5u9K2CcdN6_-Q.png\" alt=\"Checkbox hay Toggle switch - gợi ý cho thiết kế\"></p>\n<h1 id=\"5-kết-quả-onoff-có-thể-dễ-dàng-nhận-thấy\"><a href=\"#5-k%E1%BA%BFt-qu%E1%BA%A3-onoff-c%C3%B3-th%E1%BB%83-d%E1%BB%85-d%C3%A0ng-nh%E1%BA%ADn-th%E1%BA%A5y\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>5: Kết quả on/off có thể dễ dàng nhận thấy</h1>\n<p>Sử dụng Checkbox khi</p>\n<ul>\n<li>Đôi khi trạng thái on/off của toggle switch sẽ gây nhầm lẫn. Ví dụ Auto-fill form bên dưới không biết là dạng trạng thái hay ám chỉ một action cụ thể.</li>\n</ul>\n<p><img src=\"https://cdn-images-1.medium.com/max/1600/1*sKQKnqIDpchrX9U5W7SBjQ.png\" alt=\"Checkbox hay Toggle switch - gợi ý cho thiết kế\"></p>\n<h1 id=\"6-các-phần-tử-liên-quan-với-nhau\"><a href=\"#6-c%C3%A1c-ph%E1%BA%A7n-t%E1%BB%AD-li%C3%AAn-quan-v%E1%BB%9Bi-nhau\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>6: Các phần tử liên quan với nhau</h1>\n<p>Sử dụng Checkbox khi</p>\n<ul>\n<li>User chọn một trong danh sách các phần tử có liên quan với nhau về một chủ đề</li>\n</ul>\n<p><img src=\"https://cdn-images-1.medium.com/max/1600/1*Os0WnvzbzqcYcG9sQoKDjA.png\" alt=\"Checkbox hay Toggle switch - gợi ý cho thiết kế\"></p>\n<p>Sử dụng Toggle switch khi các phần tử này là những tính năng độc lập</p>\n<p><img src=\"https://cdn-images-1.medium.com/max/1600/1*U-blR633uHKtHEypNOVVOg.png\" alt=\"Checkbox hay Toggle switch - gợi ý cho thiết kế\"></p>\n<h1 id=\"7-một-lựa-chọn\"><a href=\"#7-m%E1%BB%99t-l%E1%BB%B1a-ch%E1%BB%8Dn\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>7: Một lựa chọn</h1>\n<p>Sử dụng Checkbox khi</p>\n<ul>\n<li>Muốn khuyến khích user chọn, hoặc bắt buộc user phải chọn</li>\n</ul>\n<p><img src=\"https://cdn-images-1.medium.com/max/1600/1*MBRgqod0uJHCZkKjCB05mQ.png\" alt=\"Checkbox hay Toggle switch - gợi ý cho thiết kế\"></p>\n<p>Sử dụng Toggle switch khi đang cung cấp cho một user option chứ ko bắt buộc hay gợi ý</p>\n<p><img src=\"https://cdn-images-1.medium.com/max/1600/1*LUrW1twRyE4f0jzreot3dA.png\" alt=\"Checkbox hay Toggle switch - gợi ý cho thiết kế\"></p>\n<p><a href=\"https://uxplanet.org/checkbox-vs-toggle-switch-7fc6e83f10b8\">Link bài gốc tác giả Saadia Minhas</a></p>","timeToRead":4,"excerpt":"1: Thực hiện thay đổi của user ngay lập tức 2: Màn hình settings 3: Nhiều lựa chọn 4: Khi thay đổi lựa chọn ảnh hưởng đến các lựa chọn khác…","frontmatter":{"title":"Checkbox hay Toggle switch - gợi ý cho thiết kế","cover":"https://cdn-images-1.medium.com/max/1600/1*hiOlnb_Z7HEzr6yxn2qHsA.png","date":"2018-08-21","category":null,"tags":["ux-ui"],"desc":"7 trường hợp thường thấy trong thiết kế form"},"fields":{"slug":"/2018-08-21-huong-dan-su-dung-checkbox-va-toggle-switch"}}},"pathContext":{"slug":"/2018-08-21-huong-dan-su-dung-checkbox-va-toggle-switch","prev":{"frontmatter":{"title":"Cách sử dụng refs trong React","desc":"Refs dùng để truy cập trực tiếp đến DOM trong React, nó sẽ rất hữu dụng ví dụ trong trường hợp chúng ta muốn thay đổi value của input mà ko muốn ử dụng props hoặc re-render lại toàn bộ component","type":"post","category":null,"tags":["react"],"date":"2018-08-24","cover":""},"fields":{"slug":"/2018-08-24-huong-dan-su-dung-refs-trong-react"}},"next":{"frontmatter":{"title":"Hướng dẫn viết query data cho Cloud Firestore - Phần 3","desc":"Series hướng dẫn sử dụng các service Firebase","type":"post","category":null,"tags":["firestore","firebase"],"date":"2018-08-19","cover":""},"fields":{"slug":"/2018-08-19-huong-dan-viet-query-data-tren-firestore"}}}}

